@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque&family=Londrina+Outline&family=Rampart+One&family=ZCOOL+KuaiLe&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#000}.label{font-size:1.1em;font-family:'Londrina Outline', cursive;letter-spacing:3px;color:#FFF;font-weight:bold}.App{text-align:center}.App .App-header{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.App .App-header .container{border:2px solid #fff;border-radius:4px;width:47.5vw;min-width:300px;height:47.5vh;min-height:300px;display:flex;justify-content:space-evenly;align-items:center}.App .App-header .container #drum-machine{width:270px;height:270px;display:grid;grid-template-columns:30% 30% 30%;grid-gap:5px;padding:20px;justify-content:center}.App .App-header .container #drum-machine .drum-pad{font-family:'ZCOOL KuaiLe', cursive;font-size:1.3em;font-weight:bolder;border:2px solid #fff;border-radius:12px;margin:2px;padding:10px}.App .App-header .container #display{width:270px;height:270px;font-family:'ZCOOL KuaiLe', cursive}.App .App-header .container #display .bankButtons{display:flex;justify-content:space-between}.App .App-header .container #display .bankButtons .btn{border:2px solid #fff;font-weight:bold}.App .App-header .container #display .record{color:#FFF;overflow:auto;width:100%;max-height:63px;padding:3px;border:2px solid #fff;border-radius:5px;line-height:1.1em;font-family:'Darker Grotesque', sans-serif;font-weight:bold;opacity:0.8}.App .App-header .container #display .recordingButtons{display:flex;justify-content:space-between}.App .App-header .container #display .recordingButtons .btn{font-size:0.7em;padding:5px;font-weight:bold;letter-spacing:2px}.App .App-header .container #display .speed{margin-top:-30px}.App .App-header #da3ker{color:white;font-size:1em;letter-spacing:2px;font-family:'Rampart One', cursive}.slider{-webkit-appearance:none;background:#ffc107;height:3px;border-radius:2px;opacity:0.7;border:1px solid #FFF;outline:none}.slider:hover{opacity:1;border:none}.slider::-webkit-slider-thumb{-webkit-appearance:none;width:14px;height:14px;background:#FFF;cursor:pointer;border-radius:50%}.slider::-webkit-slider-thumb:hover{border:2px solid #ffc107}.slider::-moz-range-thumb{width:14px;height:14px;background:#FFF;cursor:pointer;border-radius:50%}

